@import '../../common/mixins';
@import '../../common/vars';

/**
 * PANEL
 */

.panel {
    border-top: 2px solid #329993;
    padding-top: 30px;
    margin-bottom: 80px;
    &-image {
        margin-bottom: 20px;
        max-width: 300px;
        margin: 0 auto;
        a {
            img {
                width: 100%;
                margin-bottom: 10px; } } }
    &-value {
        margin-bottom: 10px;
        text-align: center;
        font-size: 20px; }
    &-button {
        text-align: center;
        margin-bottom: 60px; } }
