@import '../../common/mixins';
@import '../../common/vars';

/**
 * HEADER
 */

.header {
	background-color: #329993;
	margin-bottom: 40px;
	&-title {
		margin-top: 50px;
		text-align: center;
		margin-bottom: 30px;
		h1 {
			color: #fff;
			font-size: 40px; } }
	&-subtitle {
		text-align: center;
		margin-bottom: 50px;
		h2 {
			color: #fff;
			font-size: 16px;
			text-transform: uppercase; } }
	&-mag {
		margin-bottom: 20px;
		display: flex;
		justify-content: space-between;
		&_phone {
			a {
				display: flex;
				text-decoration: none;
				align-items: center;
				img {
					margin-right: 5px; }
				span {
					display: block;
					color: #fff; } } } } }

@media (max-width: 650px) {
	.header {} }
