@import '../../common/mixins';
@import '../../common/vars';

/**
 * REPAIRS
 */

.repairs {
    margin-bottom: 80px;
    &-images {
        display: flex;
        margin-bottom: 10px;
        &_vannado {
            padding-top: 78%;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center center;
            width: 100%;
            img {
                display: none;
                width: 100%;
                height: 100%; } } }
    &-value {
        margin-bottom: 10px;
        text-align: center;
        font-size: 20px; }
    &-button {
        text-align: center;
        margin-bottom: 80px; }
    &-options {
        margin-bottom: 10px;
        p {
            font-size: 18px; } } }
